<template lang="pug">
.footer
    .footer__layout
    .container
        .footer__content
            .footer__copyrights (c)2018 WOODER
            .footer__title
                .footer__logo w<span><hr>oo</span>der
            .footer__author Designed by
                a(href="https://vk.com/id16830545") Safonov Maksim
</template>

<script>
export default {
    name: "Footer",
    data: function(){
        return {
            greeting: "hi"
        }
    }

}
</script>

<style lang="sass" scoped>
.footer
    background: url("/dist/images/video-bg.jpg") center no-repeat
    position: relative
    padding: 20px 0
    margin-top: 90px
    .container
        position: relative
        width: 80%

.footer__layout
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%
    background: rgba(0, 0, 0, 0.8)

.footer__content
    display: flex
    justify-content: space-between
    align-items: center

.footer__copyrights
    color: #656565
    font-weight: bolder
    font-size: 14px

.footer__logo
    font-size: 22px
    margin-left: 32%
    color: #ffffff
    letter-spacing: 7px
    display: flex
    font-weight: bolder
    text-transform: uppercase
    span
        position: relative
    hr
        position: absolute
        background: rgba(190, 166, 124, 0.61)
        border: 0
        width: 82%
        height: 10%

.footer__author
    color: #656565
    a
        color: #929292
        text-decoration: none
</style>


