<template lang="pug">
.furniture(:class="{right : right}")
    .container
        .furniture-content
            .furniture-content__title {{ title }}
                hr.furniture-content__line
            .furniture-content__text Lorem ipsum dolor sit amet, consectetur adipisicing elit. Deserunt explicabo necessitatibus nisi optio rem, rerum                                  ullam vitae voluptate?
                hr.furniture-content__preline
            .furniture-content__btn {{ learn }}
                i.ion-ios-arrow-thin-right
                hr.furniture-content__underline
        .furniture__photo(:style='{ backgroundImage: `url(${image})` }')
            .furniture__layout
            .furniture__floatingtext { this.content().furniture }}
</template>

<script>
export default {
    name: "Decoration",
    props: {
      right: {
          type: Boolean,
          default: false
      }
    },
    data: function(){
        return {
            greeting: "hi",
            image: "dist/images/furniture.png",
            title: 'furniture',
            learn: "Learn more"
        }
    }

}
</script>

<style lang="sass" scoped>
.furniture
    margin: 98px 0 0 11%
    .container
        width: 85%
        display: flex
        justify-content: center

.furniture-content__title
    font-size: 31px
    text-transform: uppercase
    font-weight: bolder
    letter-spacing: 4px
    position: relative

.furniture-content__line
    position: absolute
    top: 50%
    margin: 0
    left: -50%
    width: 135px
    height: 2px
    background: #b9b8c0
    border: 0

.furniture-content
    width: 28%
    margin-left: 7px
    margin-top: 80px
    div
        text-align: left

.furniture-content__text
    margin-top: 15px
    width: 90%
    font-size: 12px
    line-height: 16px
    position: relative

.furniture-content__preline
    position: absolute
    top: 6px
    left: -25%
    width: 45px
    height: 2px
    background: rgba(190, 166, 124, 0.61)
    border: 0

.furniture-content__btn
    margin: 20px 0 0 0
    letter-spacing: 2px
    text-transform: uppercase
    font-size: 10px
    position: relative
    cursor: pointer
    font-weight: bolder
    i
        font-size: 13px
        margin-left: 10px
    &:hover
        hr
            width: 130px

.furniture-content__underline
    position: absolute
    left: -10px
    right: auto
    width: 31px
    height: 2px
    background: rgba(190, 166, 124, 0.61)
    border: 0
    transition: .7s

.furniture__photo
    position: relative
    width: 45%
    min-height: 270px
    -webkit-box-shadow: 0 0 29px -4px rgba(0,0,0,0.75)
    -moz-box-shadow: 0 0 29px -4px rgba(0,0,0,0.75)
    box-shadow: 0 0 29px -4px rgba(0,0,0,0.75)

.furniture__layout
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%
    background: rgba(37, 37, 37, 0.43)

.furniture__floatingtext
    position: absolute
    top: 40%
    font-size: 100px
    opacity: .15
    color: #afafaf
    text-transform: uppercase
    font-weight: bolder
    animation: floating1 30s ease-in-out infinite
    pointer-events: none


    @keyframes floating1
        0%
            transform: translateX(0)
        50%
            transform: translateX(300px)
        100%
            transform: translateX(0)

.furniture.right
    margin: 98px 11% 0 0
    .container
        flex-direction: row-reverse

    .furniture-content__line
        top: 50%
        left: 109%

    .furniture-content
        div
            text-align: right

    .furniture-content__preline
        left: 109%

    .furniture-content__btn
        &:hover
            hr
                width: 130px

    .furniture-content__text
        width: 100%

    .furniture-content__underline
        right: -10px
        left: auto

    .furniture__floatingtext
        animation: floating2 30s ease-in-out infinite

    @keyframes floating2
        0%
            transform: translateX(0)
        50%
            transform: translateX(-300px)
        100%
            transform: translateX(0)
</style>


