<template lang="pug">
.header
    Header__bg
    Header__top
    Header__content
</template>

<script>
import Header__bg from "./__bg/Header__bg";
import Header__top from "./__top/Header__top";
import Header__content from "./__content/Header__content";
export default {
    name: "Header",
    components: {Header__content, Header__top, Header__bg},
    data: function(){
        return {
            greeting: "hi"
        }
    }

}
</script>

<style lang="sass" scoped>
.header
    min-height: 670px
    max-height: 1000px
    position: relative
    background: rgba(0,0,0,0.8)
    padding: 14px 20px
    -webkit-box-shadow: 0 6px 43px -10px rgba(0,0,0,0.75)
    -moz-box-shadow: 0 6px 43px -10px rgba(0,0,0,0.75)
    box-shadow: 0 6px 43px -10px rgba(0,0,0,0.75)
    z-index: 3
</style>


