<template lang="pug">
.header-content
    .header-content__left
        .header-content__undertitle wooder
    .header-content__center
        .header-content__title wooder
        .header-content__btn { this.content().learn }}
            i.ion-ios-arrow-thin-right learn
    .header-content__right
        .header-content__floating-text furniture & decor
</template>

<script>
export default {
    name: "__content",
    data: function(){
        return {
            greeting: "hi"
        }
    }

}
</script>

<style lang="sass" scoped>
.header-content
    margin-top: 200px
    display: flex
    justify-content: center

.header-content__left
    width: 30%

.header-content__undertitle
    text-transform: uppercase
    font-size: 300px
    letter-spacing: 10px
    color: rgba(190, 166, 124, 0.61)
    font-weight: bolder
    text-align: center
    transform: rotate(270deg)
    opacity: .1
    animation: undertitle 30s ease-in-out infinite
    margin-left: 120px
    cursor: default
    pointer-events: none

@keyframes undertitle
    0%
        transform: rotate(270deg) translateY(0)
    50%
        transform: rotate(270deg) translateX(-900px)
    100%
        transform: rotate(270deg) translateY(0)

.header-content__center
    width: 40%
    display: flex
    flex-direction: column

.header-content__title
    text-transform: uppercase
    font-size: 65px
    letter-spacing: 15px
    color: rgba(190, 166, 124, 0.61)
    font-weight: bolder
    text-align: center
    margin: 40px 0 0 8%

.header-content__btn
    width: 32%
    border: 1px solid #ffffff
    color: #ffffff
    display: flex
    align-items: center
    justify-content: center
    text-transform: uppercase
    padding: 7px 20px
    font-size: 12px
    margin: 35px 0 0 18%
    letter-spacing: 1px
    cursor: pointer
    position: relative
    &:hover
        .learn
            opacity: 0
            transform: translateX(100%)

#learnmoreBtn
    position: absolute
    top: 0
    left: 0
    height: 100%
    width: 100%

.learn
    margin-left: 10px
    font-size: 25px
    transition: .5s

.header-content__right
    width: 30%

.header-content__floating-text
    transform: rotate(90deg)
    text-transform: uppercase
    font-size: 50px
    letter-spacing: 10px
    color: rgba(190, 166, 124, 0.61)
    font-weight: bolder
    text-align: center
    opacity: .3
    margin: 100px 0 0 0
    transition: 1s
    cursor: default
    pointer-events: none
</style>


