<template lang="pug">
#app
    Header
    Decoration(right=false)
    Decoration(right=true)
    Quality
    Video
    About
    Footer
</template>

<script>
import Header from "./Header/Header";
import Decoration from "./Decoration/Decoration";
import Quality from "./Quality/Quality";
import Video from "./Video/Video";
import About from "./About/About";
import Footer from "./Footer/Footer";
import getJSON from  "./../assets/getJSON"

export default {
  name: 'app',
    components: {Footer, About, Video, Quality, Decoration, Header},
    data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
    created: function(){
        getJSON('dist/javascripts/Languages/ru.json', function(err, data) {
            console.log(data);
        });
    }
}
</script>

<style lang="sass">


#app
  font-family: 'Avenir', Helvetica, Arial, sans-serif
  -webkit-font-smoothing: antialiased
  -moz-osx-font-smoothing: grayscale
  text-align: center
  color: #2c3e50
  overflow: hidden
//$littleFont-color: #BDBDBD
 //$main-color: rgba(190, 166, 124, 0.61)


</style>



