<template lang="pug">
.video
    .video__layout
    .video-content
        hr.video-content__rightline
        span.video-content__play.big
            i.ion-play.big
        .video-content__title
            hr.video-content__titleline
            span we do the design of any complexity
        .video-content__description
            hr.video-content__textline
            span Lorem ipsum dolor sit amet, consectetur adipisicing elit. Blanditiis ducimus facilis molestias nobisquaerat! Alias                             aspernatur, aut blanditiis culpa dicta doloremque fugiat iusto maiores officiis quos, repellendus rerum vero vitae.
        a.video-content__button(href="https://www.youtube.com/watch?v=TauJLzZuVQY")
            span.video-content__play
                i.ion-play
            span watch video
</template>

<script>
export default {
    name: "Video",
    data: function(){
        return {
            greeting: "hi"
        }
    }

}
</script>

<style lang="sass" scoped>
.video
    background: url("/dist/images/video-bg.jpg") center no-repeat
    margin-top: 117px
    padding: 60px 130px
    position: relative

.video__layout
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%
    background: rgba(0, 0, 0, 0.85)

.video-content
    border: 1px solid rgba(190, 166, 124, 0.61)
    padding: 150px 0 123px 270px
    position: relative

.video-content__rightline
    position: absolute
    width: 125px
    height: 2px
    background: #CDCDCD
    top: 50%
    right: 0
    border: 0
    transform: rotate(90deg)

.video-content__title
    color: rgba(190, 166, 124, 0.61)
    text-transform: uppercase
    font-size: 30px
    font-weight: bolder
    width: 60%
    line-height: 40px
    position: relative
    text-align: left

.video-content__titleline
    position: absolute
    width: 175px
    height: 2px
    left: -35%
    background: #CDCDCD
    border: 0
    //border: 1px solid #CDCDCD

.video-content__description
    color: #ffffff
    font-size: 12px
    width: 55%
    margin-top: 10px
    line-height: 16px
    font-weight: 300
    text-align: left
.video-content__textline
    position: absolute
    width: 39px
    height: 2px
    left: 17%
    background: rgba(190, 166, 124, 0.61)
    border: 0

.video-content__button
    color: #ffffff
    margin-top: 32px
    padding: 15px 15px
    width: 180px
    text-transform: uppercase
    font-size: 12px
    display: flex
    justify-content: center
    align-items: center
    border: 2px solid #ffffff
    cursor: pointer
    text-decoration: none
    &:hover
        .video__play
            transform: scale(1.1)

.video-content__play
    width: 15px
    height: 15px
    border-radius: 50%
    border: 2px solid #ffffff
    display: flex
    justify-content: center
    align-items: center
    margin-right: 10px
    transition: .3s

.video-content__play.big
    position: absolute
    top: -5%
    right: -5%
    border-radius: 50%
    border: 40px solid rgba(190, 166, 124, 0.61)
    opacity: .15
    width: 500px
    height: 500px
    font-size: 200px
    color: rgba(190, 166, 124, 0.61)
</style>


