<template lang="pug">
.quality
    .container
        .quality__title wooder — is a quality
        hr.quality__line
        .quality__text Cupiditate delectus eos hic ipsa, iusto magni minus officia rerum sequi sunt! <br>Lorem ipsum dolor sit amet, consectetur adipisicing elit.
        .quality__items
            .quality-item v-for="vid in this.content().vids)
                .quality__imgwrapper
                    .quality-item__img :style="{ background: `url(${vid.image}) bottom no-repeat` })
                        a(href="https://www.youtube.com/watch?v=TauJLzZuVQY" class="quality-item__href")
                            span.quality-item__play
                                i.ion-play
                hr.quality-item__line
                .quality-item__description { vid.desc }}
            .quality-item v-for="vid in this.content().vids)
                .quality__imgwrapper
                    .quality-item__img :style="{ background: `url(${vid.image}) bottom no-repeat` })
                        a(href="https://www.youtube.com/watch?v=TauJLzZuVQY" class="quality-item__href")
                            span.quality-item__play
                                i.ion-play
                hr.quality-item__line
                .quality-item__description { vid.desc }}
            .quality-item v-for="vid in this.content().vids)
                .quality__imgwrapper
                    .quality-item__img :style="{ background: `url(${vid.image}) bottom no-repeat` })
                        a(href="https://www.youtube.com/watch?v=TauJLzZuVQY" class="quality-item__href")
                            span.quality-item__play
                                i.ion-play
                hr.quality-item__line
                .quality-item__description { vid.desc }}
</template>

<script>
export default {
    name: "Quality",
    data: function(){
        return {
            greeting: "hi"
        }
    }

}
</script>

<style lang="sass" scoped>
.quality
    margin-top: 83px
    .container
        display: flex
        flex-direction: column
        align-items: center

.quality__title
    font-size: 30px
    text-transform: uppercase
    font-weight: bolder
    letter-spacing: 3px
    text-align: center

.quality__line
    margin-top: 20px
    width: 30px
    border: 1px solid rgba(190, 166, 124, 0.61)

.quality__text
    width: 33%
    margin-top: 5px
    text-align: center
    font-size: 12px
    color: #a5a5a5
    line-height: 20px

.quality__items
    margin-top: 50px
    width: 60%
    display: flex
    justify-content: space-between

.quality-item
    transition: .5s
    width: 30%
    display: flex
    flex-direction: column
    align-items: center
    &:hover
        transform: translateY(-10%) scale(1.2)
        .quality-item__line
            width: 100%


.quality__imgwrapper
    width: 185px
    height: 185px
    border: 1px solid #EFEFEF
    border-radius: 50%
    display: flex
    justify-content: center
    align-items: center

.quality-item__img
    width: 90%
    height: 90%
    border-radius: 50%
    cursor: pointer


.quality-item__img
    margin: 5px auto
    display: flex
    justify-content: center
    align-items: center
    position: relative

.quality-item__href
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%
    overflow: hidden
    display: flex
    justify-content: center
    align-items: center

.quality-item__play
    border-radius: 50%
    background: rgba(190, 166, 124, 0.61)
    font-size: 25px
    color: #ffffff
    padding: 10px 15px
    transition: .3s
    &:hover
        background: rgba(134, 121, 89, 0.75)

.quality-item__line
    margin-top: 10px
    width: 20px
    border: 1px solid rgba(190, 166, 124, 0.61)
    transition: .5s

.quality-item__description
    text-align: center
    margin-top: 16px
    font-size: 12px
</style>


