<template lang="pug">
.header-top
    .header-top__left
        .header-top__menu-icon
            i.ion-navicon
        .header-top__menu {this.$content().menu }}
    .header-top__center
        .header-top__logo w<span><hr>oo</span>der
        .header-top__nav
            .header-top__nav-item //(v-for="item in this.content().header_nav_items) { item }}
    .header-top__right
        i.ion-ios-information-outline.header-info
        .header-top__language en
            i.ion-android-arrow-dropdown

</template>

<script>
export default {
    name: "__top",
    data: function(){
        return {
            greeting: "hi"
        }
    }
}
</script>

<style lang="sass" scoped>
.header-top
    display: flex
    align-items: center
    justify-content: space-between
    font-weight: bolder
    color: #BDBDBD
    text-transform: uppercase
    position: relative
    padding: 1% -0px 0 20px

.header-top__left
    display: flex
    align-items: center
    width: 15%
    justify-content: flex-start

.header-top__menu-icon
    font-size: 35px

.header-top__menu
    font-weight: lighter
    font-size: 10px
    margin-left: 8px

.header-top__center
    display: flex
    align-items: center
    width: 65%
    justify-content: center


.header-top__logo
    font-size: 22px
    margin-left: 50px
    color: #ffffff
    letter-spacing: 9px
    display: flex
    span
        position: relative
    hr
        position: absolute
        background: rgba(190, 166, 124, 0.61)
        border: 0
        width: 82%
        height: 10%

.header-top__nav
    display: flex
    font-size: 10px

.header-top__nav-item
    margin-left: 73px
    cursor: pointer

.header-top__right
    display: flex
    align-items: center
    width: 20%
    justify-content: flex-end

.header-info
    margin-right: 40px
    font-size: 19px
    cursor: pointer

.header-top__language
    font-size: 10px
    margin-right: 40px
    cursor: pointer
</style>


